
  
.logs{
    width: 100vw;
    height: calc(100vh - 75px);
}

  .toggle-buttons-container{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    background-color: #111214;
    border-radius: 5px;
    padding: 5px;
    justify-content: space-between;
}
.toggle-button{
    width: calc(100% / 2);
    height: 40px;
    background-color: transparent;
    border-radius: 5px;
    border: none;
    font-size: 15px;
    font-weight: bold;
}
.selected-button{
    background-color: white;
    color: black;
}
.content{
    width: 100%;
    max-width:  100%;
    overflow-y: auto;
    overflow-x: hidden;
    height: calc(100% - 110px);
}